body {
  background-color: #cacaca;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 18px;
  font-weight: 300;
  color: #000000;
}

.container {
  width: 550px;
  margin: 10px auto;
  padding: 10px;
}

.post-box, .avatar, .comment-avatar {
  border: 1px solid #000000;
}

.post-box {
  padding: 10px;
  margin-top: 15px;
  border-radius: 15px;
  background-color: #ffffff;
}

.post-header, .comment {
  display: flex;
  align-items: center;
}

.avatar, .comment-avatar {
  border-radius: 50px;
  overflow: hidden;
}

.avatar {
  width: 60px;
  height: 60px;
}

.avatar-image {
  width: 100%;
}

.post-info {
  margin-left: 15px;
}

.date {
  margin-top: 5px;
  font-size: 14px;
}

.post-content, .comment {
  margin-top: 10px;
}

.content-image {
  width: 100%;
}

.comments {
  font-size: 12px;
}

h1 {
  padding: 0;
  font: inherit;
}

.post-box:first-child, .comment:first-child {
  margin-top: 0;
}

.comment-avatar {
  width: 30px;
  height: 30px;
}

.comment-body {
  display: block;
  width: 100%;
  margin-left: 10px;
}

.comment-header {
  display: flex;
  justify-content: space-between;
}

.comment-content {
  margin-top: 5px;
}

.load-button {
  display: block;
  margin: 5px auto;
  background: none;
  border: none;
  font: inherit;
  text-decoration: underline;
  cursor: pointer;
}
